import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {SupplyRequestPageComponent} from './supply-request.component';

const routes: Routes = [
  {
    path: '',
    component: SupplyRequestPageComponent,
  },
  {
    path: 'form',
    loadChildren: () => import('./form-supply-request/form-supply-request.module')
      .then(module => module.FormSupplyRequestPagePageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SupplyRequestPageRoutingModule { }
